import {
	GET_PROFILE_DETAILS,
	GET_PROFILE_DETAILS_SUCCESS,
	ADD_POST,
	COMMENT_POST,
	DELETE_POST,
	LIKE_POST
} from './constants'

export const loadProfileDetailsSuccess = profileDetails => ({
	type: GET_PROFILE_DETAILS_SUCCESS,
	profileDetails
})

export const loadProfileDetails = () => ({
	type: GET_PROFILE_DETAILS
})

export const likePost = id => ({
	type: LIKE_POST,
	id
})

export const commentPost = payload => ({
	type: COMMENT_POST,
	payload
})

export const addPost = image => ({
	type: ADD_POST,
	image
})

export const deletePost = id => ({
	type: DELETE_POST,
	id
})
