import { takeLatest, call, put } from 'redux-saga/effects'
import moment from 'moment'

import { loadProfileDetailsSuccess } from './actions'

import { GET_PROFILE_DETAILS, LIKE_POST, COMMENT_POST, ADD_POST, DELETE_POST } from './constants'

import { getProfileDetails } from '../../utils/service/profileService'

function* fetchProfileDetails() {
	try {
		let profileDetails = ''
		profileDetails = localStorage.getItem('profileDetails')
		if (!profileDetails) {
			profileDetails = yield call(getProfileDetails)
			profileDetails = profileDetails.map(each => ({
				...each,
				liked: false,
				comments: []
			}))
			localStorage.setItem('profileDetails', JSON.stringify(profileDetails));
		} else {
			profileDetails = JSON.parse(profileDetails)
		}
		yield put(loadProfileDetailsSuccess(profileDetails))
	} catch (error) {
		// todo
	}
}

function* likePost(param) {
	try {
		let profileDetails = ''
		profileDetails = JSON.parse(localStorage.getItem('profileDetails'))
		if(profileDetails[param.id].liked) {
			profileDetails[param.id].liked = false
			profileDetails[param.id].likes = +(profileDetails[param.id].likes) - 1
		} else {
			profileDetails[param.id].liked = true
			profileDetails[param.id].likes = +(profileDetails[param.id].likes) + 1
		}
		localStorage.setItem('profileDetails', JSON.stringify(profileDetails));
		yield put(loadProfileDetailsSuccess(profileDetails))
	} catch (error) {
		console.log(error)
		// todo
	}
}

function* commentPost(param) {
	try {
		let profileDetails = ''
		profileDetails = JSON.parse(localStorage.getItem('profileDetails'))
		console.log(param)
		profileDetails[param.payload.id].comments.unshift({
			data: param.payload.comment,
			timestamp: moment().format('YYYY-MM-DD HH:mm:ss')
		})
		localStorage.setItem('profileDetails', JSON.stringify(profileDetails));
		yield put(loadProfileDetailsSuccess(profileDetails))
	} catch (error) {
		// todo
	}
}

function* addPost(param) {
	try {
		let profileDetails = ''
		profileDetails = JSON.parse(localStorage.getItem('profileDetails'))
		profileDetails.unshift({
			Image: param.image,
			timestamp: moment().format('YYYY-MM-DD HH:mm:ss'),
			likes: 0,
			liked: false,
			comments: []
		})
		localStorage.setItem('profileDetails', JSON.stringify(profileDetails));
		yield put(loadProfileDetailsSuccess(profileDetails))
	} catch (error) {
		// todo
	}
}

function* deletePost(param) {
	try {
		let newDetals = []
		const profileDetails = JSON.parse(localStorage.getItem('profileDetails'))
		profileDetails.forEach((value, index) => {
			if(index !== param.id) {
				newDetals.push(value)
			}
		})
		localStorage.setItem('profileDetails', JSON.stringify(newDetals));
		yield put(loadProfileDetailsSuccess(newDetals))
	} catch (error) {
		// todo
	}
}

function* deletePostSaga() {
	yield takeLatest(DELETE_POST, deletePost)
}

function* addPostSaga() {
	yield takeLatest(ADD_POST, addPost)
}

function* likePostSaga() {
	yield takeLatest(LIKE_POST, likePost)
}

function* commentPostSaga() {
	yield takeLatest(COMMENT_POST, commentPost)
}

function* fetchProfileDetailsSaga() {
	yield takeLatest(GET_PROFILE_DETAILS, fetchProfileDetails)
}

export default [fetchProfileDetailsSaga, likePostSaga, commentPostSaga, addPostSaga, deletePostSaga]
