import React, { Component } from 'react'
import { connect } from 'react-redux';
import App from '../../components/App'
import { loadProfileDetails, likePost, commentPost, addPost, deletePost } from './actions'

import './index.css'
class AppContainer extends Component {
  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg bg-secondary fixed-top navbar-shrink" id="mainNav">
          <div className="container">
            <span className="navbar-brand js-scroll-trigger" href="#page-top">ObserveAI Hackerearth Test</span>
          </div>
        </nav>
        <App {...this.props} />
        <div className="copyright py-4 text-center text-white mt-5">
          <div className="container">
            <small>Copyright Â© Ashwani 2018</small>
          </div>
        </div>
      </div>
    )
  }
}


const mapStateToProps = state => {
  return {
    profileDetails: state.getIn(['AppReducer', 'profileDetails'])
  }
}

const mapDispatchToProps = dispatch => ({
  loadProfileDetails: () => dispatch(loadProfileDetails()),
  likePost: id => dispatch(likePost(id)),
  commentPost: (comment, id) => dispatch(commentPost({ comment, id })),
  addPost: image => dispatch(addPost(image)),
  deletePost: id => dispatch(deletePost(id))
})

export default connect(mapStateToProps, mapDispatchToProps)(AppContainer);
