import React, { Component } from 'react';
import moment from 'moment'
import { TiHeartOutline, TiHeartFullOutline } from 'react-icons/ti'
import { IoMdMore } from 'react-icons/io'
import {
	Card, CardImg, CardText, CardBody, Dropdown, Button,
	DropdownToggle, DropdownMenu, DropdownItem, CardSubtitle
} from 'reactstrap';

import './index.css'

class ImageCard extends Component {

	state = {
		dropdownOpen: false
	}

	toggleDropDown = () => {
		const { dropdownOpen } = this.state
		this.setState({
			dropdownOpen: !dropdownOpen
		})
	}

	render() {
		const { dropdownOpen } = this.state
		const { imageDetails, openImage, likePost, id, deletePost } = this.props
		return (
			<div className="mt-3">
				<Card>
					<CardImg
						top
						width="100%"
						src={imageDetails.get('Image')}
						alt="Card image cap"
						className="image-size pointer"
						onClick={() => openImage(id)}
					/>
					<CardBody>
						<CardSubtitle>
							<strong>
								{
									imageDetails.get('liked') ?
										<TiHeartFullOutline color="red" className="mr-2 pointer f-1-5" onClick={() => likePost(id)} /> :
										<TiHeartOutline className="mr-2 pointer f-1-3" onClick={() => likePost(id)} />
								}
								{`${imageDetails.get('likes') ? imageDetails.get('likes') : 0} Likes`}
								<span className="pl-2">|</span>
								<Button className="transparent-btn border-none clr-black" onClick={() => openImage(id)}>
									<strong>
										{`${imageDetails.get('comments') ? imageDetails.get('comments').size : 0}`}
									</strong>
									<span className="pl-2">Comments</span>
								</Button>
							</strong>
							<span className="float-right">
								<Dropdown isOpen={dropdownOpen} toggle={this.toggleDropDown}>
									<DropdownToggle className="transparent-btn p-0">
										<IoMdMore color="black" />
									</DropdownToggle>
									<DropdownMenu>
										<DropdownItem onClick={() => deletePost(id)}>
											Delete
										</DropdownItem>
									</DropdownMenu>
								</Dropdown>
							</span>
						</CardSubtitle>
						<CardText>
							<small>{moment(imageDetails.get('timestamp')).fromNow()}</small>
						</CardText>
					</CardBody>
				</Card>
			</div>
		);
	}
}

export default ImageCard
