import axios from 'axios'

export function getProfileDetails() {
	return axios.get(
		`https://cors-anywhere.herokuapp.com/http://starlord.hackerearth.com/insta`,
		{
			headers: { 'Access-Control-Allow-Origin': '*' },
			crossdomain: true
		}
	)
		.then(response => response.data)
		.catch(err => {
			throw err
		})
}
