import React, { Component, Fragment } from 'react';
import moment from 'moment'
import { TiHeartOutline, TiHeartFullOutline } from 'react-icons/ti'
import { IoIosClose } from 'react-icons/io'
import { Row, Col, Input, Button } from 'reactstrap';

import './index.css'

class EachImage extends Component {

	state = {
		comment: ''
	}

	changeComment = e => {
		this.setState({
			comment: e.target.value.trim()
		})
	}

	addComment = () => {
		const { commentPost, id } = this.props
		const { comment } = this.state
		if (comment !== '')
			commentPost(comment, id)
		this.setState({
			comment: ''
		})
	}

	render() {
		const { details, likePost, id, closeModal } = this.props
		const { comment } = this.state
		return (
			<Row className="p-0">
				{
					id !== '' ? (
						<Fragment>
							<Col sm={7}>
								<img src={details.getIn([id, 'Image'])} alt="Image" className="full-width container-image" />
							</Col>
							<Col sm={5} className="mt-3  pr-5">
								<Row>
									<Col sm={12} className="border-btm pb-2 mb-2">
										<strong>
											{
												details.getIn([id, 'liked']) ?
													<TiHeartFullOutline color="red" className="mr-2 pointer f-1-5" onClick={() => likePost(id)} /> :
													<TiHeartOutline className="mr-2 pointer f-1-3" onClick={() => likePost(id)} />
											}

											{`${details.getIn([id, 'likes']) ? details.getIn([id, 'likes']) : 0} Likes`}
										</strong>
										<IoIosClose onClick={closeModal} className="pointer float-right f-1-5" />
									</Col>
									<Col sm={12} className="comment-section">
										{
											details.getIn([id, 'comments']).map((value, index) =>
												<div key={`comment-${index}`} className="comment-div mb-2">
													<div>{value.get('data')}</div>
													<small>{moment(value.get('timestamp')).fromNow()}</small>
												</div>
											)
										}
									</Col>
									<Col sm={12} className="mt-3">
										<Input
											type="textarea"
											value={comment}
											onChange={this.changeComment}
										/>
										<Button onClick={this.addComment} className="mt-2 float-right">Comment</Button>
									</Col>
								</Row>
							</Col>
						</Fragment>
					) : null
				}

			</Row>
		);
	}
}

export default EachImage
