import React, { Component } from 'react';
import { Button, Modal, Input, ModalBody, Container, Row, Col } from 'reactstrap';
import { fromJS } from 'immutable';

import ImageCard from './ImageCard'
import EachImage from './EachImage'

import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';

class App extends Component {

	state = {
		openImageStatus: false,
		selectedId: '',
		imagePreviewUrl: '',
		fileName: ''
	}

	closeToggle = () => {
		this.setState({
			openImageStatus: false,
			details: fromJS([]),
			selectedId: ''
		})
	}

	openImage = id => {
		this.setState({
			openImageStatus: true,
			selectedId: id
		})
	}

	componentDidMount() {
		const { loadProfileDetails } = this.props
		loadProfileDetails()
	}

	selectImage = e => {
		e.preventDefault()
		const reader = new FileReader()
		const file = e.target.files[0]
		const allowedImg = ['data:image/jpeg', 'data:image/png', 'data:image/jpg', 'data:image/JPG', 'data:image/JPEG']
		if (reader) {
			reader.readAsDataURL(file)
			reader.onloadend = () => {
				const type = reader.result.substring(0, 15)
				let flag = false
				allowedImg.forEach(value => {
					if (type.startsWith(value)) {
						flag = true
					}
				})
				if (flag) {
					this.setState({
						imagePreviewUrl: reader.result
					})
				}
			}
		} else {
			e.target.value = null
		}
	}

	uploadImage = () => {
		const { addPost } = this.props
		const { imagePreviewUrl } = this.state
		if (imagePreviewUrl !== '') {
			addPost(imagePreviewUrl)
			this.setState({
				imagePreviewUrl: ''
			})
		}
		this.fileInput.value = ''
	}

	render() {
		const { profileDetails, likePost, commentPost, deletePost } = this.props
		const { openImageStatus, selectedId } = this.state
		return (
			<Container className="mt-90">
				<Row>
					<Col sm={{ size: 3, offset: 6 }}>
						<Input type="file" onChange={this.selectImage} ref={ref => this.fileInput = ref} />
					</Col>
					<Col sm={3}>
						<Button onClick={this.uploadImage}>Upload Post</Button>
					</Col>
				</Row>
				<Row>
					{
						profileDetails.size > 0 ? profileDetails.map((value, index) =>
							<Col sm={12} md={6} lg={4} key={index}>
								<ImageCard
									imageDetails={value}
									openImage={this.openImage}
									likePost={likePost}
									deletePost={deletePost}
									id={index}
								/>
							</Col>
						) : null
					}
				</Row>
				<Modal isOpen={openImageStatus} toggle={this.closeToggle} size="lg">
					<ModalBody className="p-0">
						<EachImage
							details={profileDetails}
							closeModal={this.closeToggle}
							likePost={likePost}
							id={selectedId}
							commentPost={commentPost}
						/>
					</ModalBody>
				</Modal>
			</Container>
		);
	}
}

export default App;
